import { Action } from '@ali/4ever-cangjie';
import Table from '../../mo/models';

export const TABLE_SCROLL = Symbol('TABLE_SCROLL');

export function scrollTable(table: HTMLElement | null) {
  return {
    type: TABLE_SCROLL,
    payload: {
      table,
    },
  };
}

interface TableScrollTablePayload {
  node: Table;
  delta: number;
  maxWidth?: number;
}

interface TableScrollTableAction {
  type: typeof TABLE_SCROLL;
  payload: TableScrollTablePayload;
}

export function isTableScrollTableAction(
  action: Action,
): action is TableScrollTableAction {
  return action.type === TABLE_SCROLL;
}
